@import url("https://fonts.googleapis.com/css?family=Roboto:400,100,900");
@import 'normalize';
@import 'loader';
@import 'typed';

/* my style here */

body {
    background: #2c3e50;
    font-family: "Roboto", helvetica;
}

main {
    display: none;
}

#header {
    background: #EB3349; /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #EB3349 , #F45C43); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #EB3349 , #F45C43); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: left;
    width:100vw;
    height:100vh;
    transition:all 999999999s;
    .text-wrapper {
        padding: 0 5%;    
    }  
}

#section2 {
    background: #02AAB0; /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, #02AAB0 , #00CDAC); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, #02AAB0 , #00CDAC); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */       
}
.section{
    
}
.wrapper {
    width:45%;
    margin:0 auto;
}
.wrapper img {
    width:100%;
}
.description {
    background:rgba(0,0,0,0.3);
    padding: 15px;  
}
.desc {
    margin-bottom:10px;
    color:#bfbfbf;
}
.tech {
    color:#999;
}

h1{
    font-size: 5.5em;   
    font-weight: 900;
    color: #fff;
    margin:0;
}

h2 {
    font-size:4em;
    font-family: "Roboto", helvetica;
    font-weight: 100;
    margin:0 auto;
    color: #fff;
    width:80%;
    text-align: left;

}
h3 {
    font-size:1.75em;
    font-family: "Roboto", helvetica;
    font-weight: 600;
    margin:0;
    width:100%;
    text-align: left;
    color:#fff;
}
.section2 h3 {
    text-align: center;
    margin:0 auto;
} 

p {
    font-family: "Roboto", helvetica;
    font-weight: 100;
    font-size: 1.25em;
    color: #fff;
    text-align: left;
    margin:0 auto;
}

#slide12 h3 {
/*    width:80%;*/
    text-align: left;
}
.intro p {
    color: #fff;
    width:100%;    
}

/* Centered texts in each section
* --------------------------------------- */



/* Overwriting styles for the navigation dots (to place it where we want)
* --------------------------------------- */
.fp-slidesNav.bottom{
    bottom: 25px;
    color:#fff;
}



