.fa {
    color:#EB3349;
    margin-top: 5px 3px 0 3px;
}
h3 span {
    color:#EB3349;
}

#credit {
    font-family: helvetica;
    // position:absolute;
    // bottom:25px;
    margin: 0 auto;   
    color:white;    
    z-index:1;
    font-size: 0.8em;
}

#credit p {
    text-align: center;
}

p a {
    color:#EB3349;
}

.story h3 {
    font-size: 2.8em;   
    text-align:left;
    width:80%;
    margin: 2% auto;

}

.story img {
    width:45%;
    border-radius:5px;
    box-shadow: 10px 10px 10px rgba(0,0,0,0.5);
}

.hidden {
    display:none;
}
.story strong{
    cursor: pointer;
}
.slides {
    display:none;
}

figcaption {
    color:#fff;
    font-size: .8em;
    margin-top: 5px;
}

figure {
    margin:0;
    padding:0;
}

#storySlide8 img {
    width:40%;
    box-shadow:none;
}

canvas {  
    display:block;
    vertical-align:center;
    height:100%;
}


@media screen and (max-device-width: 769px) {
    .wrapper {
        width:80%;
    }
    .wrapper h3{
        font-size: 2.8em;
    }
    .description p {
        font-size: 1.8em;
    }

    .story img {
    width:80%;
    }
   

}


